@import url("https://fonts.googleapis.com/css2?family=Lobster&display=swap");
@import "stylesheets/variables.scss";
@import "stylesheets/responsive.scss";

.mainVendorCardContainer {
  margin-top: 60px;
  width: 100%;
  display: grid;
  grid-template-columns: repeat(auto-fill, 260px);
  grid-gap: 40px;
  justify-content: center;
  @media #{$tablet_device} {
    margin-top: 20px;
  }
  @media #{$mobile_device} {
    margin-top: 10px;
  }
}



.vendorListCardContainer {
  min-height: 100px;
  height: fit-content;
  cursor: pointer;
  border-radius: 5px;
  background-color:#fff;
  padding:10px 25px;
  display: flex;
  align-items: center;
  flex-direction: column;
  justify-content: space-evenly;
}

.vendorListNameStyle {
  font-size: 15px;
  color: #607d8b;
  text-align: center;
}



.vendorListCardContainer h4 {
  margin: 2px;
  font-weight:600;
  color:#000;
  letter-spacing:.7px;
  
  line-height:1.5;
  font-size: 17px;
  font-family: $primary-font-family;
  @media #{$tablet_device} {
    font-size: $large-font-tablet;
  }
  @media #{$mobile_device} {
    font-size: 18px;
  }
}

.vendorListCardStyle {
  font-weight: 600;
  line-height: 1.67vmax;
}
